const buttons = {
  spells: { label: "FFD20.BrowseSpells" },
  items: { label: "FFD20.BrowseItems" },
  bestiary: { label: "FFD20.BrowseBestiary" },
  feats: { label: "FFD20.BrowseFeats" },
  classes: { label: "FFD20.BrowseClasses" },
  races: { label: "FFD20.BrowseRaces" },
  buffs: { label: "FFD20.BrowseBuffs", wide: true },
};

/**
 * @param {Event} event - Triggering event
 */
function compendiumButtonClick(event) {
  event.preventDefault();
  const type = event.target.dataset.category;
  ffd20.applications.compendiums[type]._render(true, { focus: true });
}

/**
 * Render compendium browser buttons.
 *
 * @param {CompendiumDirectory} app
 * @param {HTMLElement|JQuery} html
 * @param {object} options
 */
Hooks.on("renderCompendiumDirectory", async (app, html) => {
  if (html instanceof jQuery) html = html[0]; // v12/v13 cross compatibility

  let element;
  if (game.release.generation >= 13) {
    element = document.createElement("section");
    html.append(element);
  } else {
    element = html.querySelector("footer.directory-footer");
  }
  element.classList.add("ffd20", "action-buttons");

  for (const [category, info] of Object.entries(buttons)) {
    const button = document.createElement("button");
    button.type = "button";
    button.dataset.category = category;
    button.classList.add("compendium", category);
    button.innerText = game.i18n.localize(info.label);
    if (info.wide) button.classList.add("colspan-2");
    element.append(button);
    button.addEventListener("click", compendiumButtonClick);
  }
});

// Add compendium sidebar context options
Hooks.on("getCompendiumDirectoryEntryContext", (html, entryOptions) => {
  // Add option to enable & disable pack
  entryOptions.unshift(
    {
      name: game.i18n.localize("FFD20.CompendiumBrowser.HidePack"),
      icon: '<i class="fa-solid fa-low-vision"></i>',
      condition: ([li]) => {
        const pack = game.packs.get(li.dataset.pack);
        return pack.config.ffd20?.disabled !== true;
      },
      callback: ([li]) => {
        const pack = game.packs.get(li.dataset.pack);
        pack.configure({ ffd20: { disabled: true } });
      },
    },
    {
      name: game.i18n.localize("FFD20.CompendiumBrowser.ShowPack"),
      icon: '<i class="fa-solid fa-eye"></i>',
      condition: ([li]) => {
        const pack = game.packs.get(li.dataset.pack);
        return pack.config.ffd20?.disabled === true;
      },
      callback: ([li]) => {
        const pack = game.packs.get(li.dataset.pack);
        pack.configure({ ffd20: { disabled: false } });
      },
    }
  );
});
